import React, { useState } from 'react'

interface FormErrors {
  name?: string
  email?: string
  company?: string
  message?: string
}

interface FormTouched {
  name: boolean
  email: boolean
  company: boolean
  message: boolean
}

const Contact: React.FC = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    company: '',
    message: ''
  })

  const [errors, setErrors] = useState<FormErrors>({})
  const [touched, setTouched] = useState<FormTouched>({
    name: false,
    email: false,
    company: false,
    message: false
  })
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [isSubmitted, setIsSubmitted] = useState(false)

  const validateField = (name: string, value: string): string | undefined => {
    switch (name) {
      case 'name':
        if (!value.trim()) return 'Please enter your full name'
        if (value.trim().length < 2) return 'Name must be at least 2 characters'
        if (value.trim().length > 50) return 'Name must be less than 50 characters'
        if (!/^[a-zA-Z\s'-]+$/.test(value.trim())) return 'Name can only contain letters, spaces, hyphens, and apostrophes'
        return undefined
      
      case 'email':
        if (!value.trim()) return 'Please enter your email address'
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
        if (!emailRegex.test(value.trim())) return 'Please enter a valid email address'
        return undefined
      
      case 'company':
        if (value.trim().length > 100) return 'Company name must be less than 100 characters'
        return undefined
      
      case 'message':
        if (!value.trim()) return 'Please enter your message'
        if (value.trim().length < 10) return 'Message must be at least 10 characters'
        if (value.trim().length > 1000) return 'Message must be less than 1000 characters'
        return undefined
      
      default:
        return undefined
    }
  }

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {}
    let isValid = true

    Object.keys(formData).forEach(key => {
      const error = validateField(key, formData[key as keyof typeof formData])
      if (error) {
        newErrors[key as keyof FormErrors] = error
        isValid = false
      }
    })

    setErrors(newErrors)
    return isValid
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))

    // Clear error when user starts typing
    if (errors[name as keyof FormErrors]) {
      setErrors(prev => ({
        ...prev,
        [name]: undefined
      }))
    }
  }

  const handleInputBlur = (e: React.FocusEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setTouched(prev => ({
      ...prev,
      [name]: true
    }))

    const error = validateField(name, value)
    setErrors(prev => ({
      ...prev,
      [name]: error
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    // Mark all fields as touched
    setTouched({
      name: true,
      email: true,
      company: true,
      message: true
    })

    if (!validateForm()) {
      return
    }

    setIsSubmitting(true)
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      console.log('Form submitted:', formData)
      setIsSubmitted(true)
      
      // Reset form after showing success message
      setTimeout(() => {
        setFormData({
          name: '',
          email: '',
          company: '',
          message: ''
        })
        setErrors({})
        setTouched({
          name: false,
          email: false,
          company: false,
          message: false
        })
        setIsSubmitted(false)
      }, 3000)
    } catch (error) {
      console.error('Form submission error:', error)
      alert('Sorry, there was an error sending your message. Please try again.')
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <section 
      id="contact" 
      className="section-padding bg-gradient-to-b from-slate-50 to-white"
      aria-labelledby="contact-heading"
    >
      <div className="container-max">
        <div className="text-center mb-16 sm:mb-20 lg:mb-24">
          <div className="inline-flex items-center px-6 py-3 bg-blue-50 text-blue-700 rounded-full text-sm font-semibold mb-8 border border-blue-200">
            <span className="w-2 h-2 bg-blue-500 rounded-full mr-3"></span>
            Get In Touch
          </div>
          <h2 
            id="contact-heading"
            className="text-5xl sm:text-6xl md:text-7xl lg:text-8xl font-bold text-slate-900 mb-8 leading-[1.1] tracking-tight"
          >
            Ready to{' '}
            <span className="bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
              Get Started?
            </span>
          </h2>
          <p className="text-xl sm:text-2xl lg:text-3xl text-slate-600 max-w-4xl mx-auto leading-relaxed font-light">
            Connect with our enterprise team to discuss your carbon asset strategy 
            and explore how our platform can maximize your land's value.
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 lg:gap-16">
          {/* Contact Form */}
          <div className="bg-white rounded-3xl p-8 lg:p-12 border border-slate-200/50 shadow-sm">
            <h3 className="text-3xl lg:text-4xl font-bold text-slate-900 mb-8">Send us a message</h3>
            
            {isSubmitted && (
              <div className="mb-6 p-4 bg-green-100 border border-green-300 rounded-lg">
                <div className="flex items-center space-x-3">
                  <div className="w-6 h-6 bg-green-500 rounded-full flex items-center justify-center">
                    <div className="w-2 h-2 bg-white rounded-full"></div>
                  </div>
                  <div>
                    <h4 className="text-green-800 font-semibold">Message Sent Successfully!</h4>
                    <p className="text-green-700 text-sm">Thank you for your message. We'll get back to you within 2-4 hours.</p>
                  </div>
                </div>
              </div>
            )}
            <form 
              onSubmit={handleSubmit} 
              className="space-y-6" 
              noValidate
              aria-label="Contact form"
            >
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                <div className="relative">
                  <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-2">
                    Full Name *
                  </label>
                  <div className="relative">
                    <input
                      type="text"
                      id="name"
                      name="name"
                      value={formData.name}
                      onChange={handleInputChange}
                      onBlur={handleInputBlur}
                      className={`w-full px-4 py-3 border rounded-xl transition-all duration-200 ${
                        errors.name && touched.name
                          ? 'border-red-300 focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-red-50'
                          : 'border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 bg-white'
                      }`}
                      placeholder="Your full name"
                    />
                    {errors.name && touched.name && (
                      <div className="absolute -bottom-1 left-0 sm:left-auto sm:right-0 transform translate-y-full z-10">
                        <div className="bg-red-600 text-white text-sm px-3 py-2 rounded-lg shadow-lg animate-in slide-in-from-top-2 duration-200 max-w-xs sm:max-w-sm">
                          <div className="flex items-center space-x-2">
                            <div className="w-1.5 h-1.5 bg-white rounded-full flex-shrink-0"></div>
                            <span>{errors.name}</span>
                          </div>
                          <div className="absolute -top-1 left-4 sm:left-auto sm:right-4 w-2 h-2 bg-red-600 transform rotate-45"></div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
                <div className="relative">
                  <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">
                    Email Address *
                  </label>
                  <div className="relative">
                    <input
                      type="email"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      onBlur={handleInputBlur}
                      className={`w-full px-4 py-3 border rounded-xl transition-all duration-200 ${
                        errors.email && touched.email
                          ? 'border-red-300 focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-red-50'
                          : 'border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 bg-white'
                      }`}
                      placeholder="your@email.com"
                    />
                    {errors.email && touched.email && (
                      <div className="absolute -bottom-1 left-0 sm:left-auto sm:right-0 transform translate-y-full z-10">
                        <div className="bg-red-600 text-white text-sm px-3 py-2 rounded-lg shadow-lg animate-in slide-in-from-top-2 duration-200 max-w-xs sm:max-w-sm">
                          <div className="flex items-center space-x-2">
                            <div className="w-1.5 h-1.5 bg-white rounded-full flex-shrink-0"></div>
                            <span>{errors.email}</span>
                          </div>
                          <div className="absolute -top-1 left-4 sm:left-auto sm:right-4 w-2 h-2 bg-red-600 transform rotate-45"></div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
              <div className="relative">
                <label htmlFor="company" className="block text-sm font-medium text-gray-700 mb-2">
                  Company
                </label>
                <div className="relative">
                  <input
                    type="text"
                    id="company"
                    name="company"
                    value={formData.company}
                    onChange={handleInputChange}
                    onBlur={handleInputBlur}
                    className={`w-full px-4 py-3 border rounded-xl transition-all duration-200 ${
                      errors.company && touched.company
                        ? 'border-red-300 focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-red-50'
                        : 'border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 bg-white'
                    }`}
                    placeholder="Your company name"
                  />
                  {errors.company && touched.company && (
                    <div className="absolute -bottom-1 left-0 sm:left-auto sm:right-0 transform translate-y-full z-10">
                      <div className="bg-red-600 text-white text-sm px-3 py-2 rounded-lg shadow-lg animate-in slide-in-from-top-2 duration-200 max-w-xs sm:max-w-sm">
                        <div className="flex items-center space-x-2">
                          <div className="w-1.5 h-1.5 bg-white rounded-full flex-shrink-0"></div>
                          <span>{errors.company}</span>
                        </div>
                        <div className="absolute -top-1 left-4 sm:left-auto sm:right-4 w-2 h-2 bg-red-600 transform rotate-45"></div>
                      </div>
                    </div>
                  )}
                </div>
              </div>
              <div className="relative">
                <div className="flex justify-between items-center mb-2">
                  <label htmlFor="message" className="block text-sm font-medium text-gray-700">
                    Message *
                  </label>
                  <span className={`text-xs ${
                    formData.message.length > 1000 
                      ? 'text-red-500' 
                      : formData.message.length > 800 
                        ? 'text-yellow-500' 
                        : 'text-gray-400'
                  }`}>
                    {formData.message.length}/1000
                  </span>
                </div>
                <div className="relative">
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleInputChange}
                    onBlur={handleInputBlur}
                    rows={5}
                    className={`w-full px-4 py-3 border rounded-xl transition-all duration-200 resize-none ${
                      errors.message && touched.message
                        ? 'border-red-300 focus:ring-2 focus:ring-red-500 focus:border-red-500 bg-red-50'
                        : 'border-slate-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 bg-white'
                    }`}
                    placeholder="Tell us about your land and any questions you have..."
                  />
                  {errors.message && touched.message && (
                    <div className="absolute -bottom-1 left-0 sm:left-auto sm:right-0 transform translate-y-full z-10">
                      <div className="bg-red-600 text-white text-sm px-3 py-2 rounded-lg shadow-lg animate-in slide-in-from-top-2 duration-200 max-w-xs sm:max-w-sm">
                        <div className="flex items-center space-x-2">
                          <div className="w-1.5 h-1.5 bg-white rounded-full flex-shrink-0"></div>
                          <span>{errors.message}</span>
                        </div>
                        <div className="absolute -top-1 left-4 sm:left-auto sm:right-4 w-2 h-2 bg-red-600 transform rotate-45"></div>
                      </div>
                    </div>
                  )}
                </div>
              </div>
              <button
                type="submit"
                disabled={isSubmitting}
                className={`w-full font-semibold py-4 px-8 rounded-xl text-lg transition-all duration-200 ${
                  isSubmitting
                    ? 'bg-slate-400 cursor-not-allowed text-white'
                    : 'bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white shadow-lg hover:shadow-xl transform hover:-translate-y-0.5'
                }`}
              >
                {isSubmitting ? (
                  <div className="flex items-center justify-center space-x-2">
                    <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    <span>Sending...</span>
                  </div>
                ) : (
                  'Send Message'
                )}
              </button>
            </form>
          </div>

          {/* Contact Information */}
          <div className="space-y-8">
            <div>
              <h3 className="text-3xl lg:text-4xl font-bold text-slate-900 mb-8">Contact Information</h3>
              <div className="space-y-8">
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-indigo-500 rounded-xl flex items-center justify-center flex-shrink-0">
                    <span className="text-white font-bold text-lg">@</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 mb-2 text-lg">Email</h4>
                    <p className="text-slate-600 text-base">enterprise@creditnature.com</p>
                  </div>
                </div>
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-indigo-500 rounded-xl flex items-center justify-center flex-shrink-0">
                    <span className="text-white font-bold text-lg">📞</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 mb-2 text-lg">Phone</h4>
                    <p className="text-slate-600 text-base">+1 (555) 123-4567</p>
                  </div>
                </div>
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-indigo-500 rounded-xl flex items-center justify-center flex-shrink-0">
                    <span className="text-white font-bold text-lg">📍</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 mb-2 text-lg">Office</h4>
                    <p className="text-slate-600 text-base">123 Carbon Street<br />San Francisco, CA 94105</p>
                  </div>
                </div>
              </div>
            </div>

            {/* Response Time */}
            <div className="bg-blue-50 rounded-3xl p-8 border border-blue-200">
              <div className="flex items-center space-x-3 mb-4">
                <div className="w-8 h-8 bg-gradient-to-br from-blue-500 to-indigo-500 rounded-lg flex items-center justify-center">
                  <span className="text-white font-bold text-sm">⚡</span>
                </div>
                <h4 className="font-semibold text-slate-900 text-lg">Quick Response</h4>
              </div>
              <p className="text-slate-600 text-base">
                Our enterprise team typically responds within 2-4 hours during business hours.
              </p>
            </div>

            {/* Enterprise Features */}
            <div className="bg-slate-50 rounded-3xl p-8">
              <h4 className="font-semibold text-slate-900 mb-6 text-lg">Enterprise Services</h4>
              <ul className="space-y-4 text-slate-600">
                <li className="flex items-center space-x-3">
                  <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                  <span>Personalized carbon measurement solutions</span>
                </li>
                <li className="flex items-center space-x-3">
                  <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                  <span>Dedicated account management</span>
                </li>
                <li className="flex items-center space-x-3">
                  <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                  <span>Priority technical support</span>
                </li>
                <li className="flex items-center space-x-3">
                  <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                  <span>Compliance and verification assistance</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Contact
